import React from "react";

const ResponsiveChip = ({ content }) => {
  return (
    <div
      style={{
        borderRadius: "5px",
        backgroundColor: "#e0e0e0",
        padding: "4px 8px",
        maxWidth: "100%",
        whiteSpace: "normal",
        fontSize: "0.8125rem",
        fontWeight: 400,
      }}
    >
      {content}
    </div>
  );
};

export default ResponsiveChip;
