import React from "react";

const useGatherDataFromApiOnMount = (arrayOfFunctions) => {
  const [apiData, getApiData] = React.useState(null);
  const ApiDataHandler = (ObjectProp, value) => {
    getApiData((prev) => ({
      ...prev,
      [ObjectProp]: value,
    }));
  };
  React.useEffect(() => {
    arrayOfFunctions.forEach((effectFn) => effectFn(ApiDataHandler));
  }, []);

  return { ApiDataHandler, apiData };
};

export default useGatherDataFromApiOnMount;
