import { Stack } from "@mui/material";
import React from "react";
import { GetCurrentDateInJalai } from "utils/GetCurrentDateInJalai";
import { GetCurrentTimeinMinutesJalai } from "utils/GetCurrentTimeinMinutesJalai";
import SingularDatePicker from "../SingularDatePicker/SingularDatePicker";
import SingularTimePicker from "../SingularTimePicker/SingularTimePicker";
import { useTranslation } from "react-i18next";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

const DateTimePicker = ({ label, onChange, errors, name }) => {
  const [UiValue, SetUiValue] = React.useState({
    Date: GetCurrentDateInJalai(),
    Time: GetCurrentTimeinMinutesJalai(),
  });
  React.useEffect(() => {
    if (Object.values(UiValue).length) {
      onChange && onChange(UiValue);
    }
  }, [UiValue]);
  const { t, i18n } = useTranslation();

  return (
    <React.Fragment>
      <Stack direction="row" spacing={1} alignItems="center">
        <SingularDatePicker
          i18n={i18n}
          label={FindLangKey("general-public-history")}
          getter={SetUiValue}
        />
        <SingularTimePicker
          label={FindLangKey("general-public-label-time")}
          getter={SetUiValue}
        />
      </Stack>
    </React.Fragment>
  );
};

export default DateTimePicker;
