import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const AddPatientToWorkList = async (data) => {
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_ADD_PATIENT_TO_WORK_LIST}`,
    true,
    {
      ...data,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      SwalAlert('success', 'ارجاع جدید ثبت شد', false);
    } else {
      SwalAlert('success', 'ارجاع جدید ثبت شد', false);
    }
  });
};
