import SwalAlert from "Global/Components/CommonComponents/SwalAlert/SwalAlert";
import { AddPatientToWorkList } from "../Adapters/AddPatientToWorkList";

export const AddPatientForNewErjaHandler = (
  inputData,
  gatheredData,
  previousSavedValue
) => {
  delete inputData.DateRange;
  delete inputData.Date;
  const formData = {
    ...inputData,
    timeHozoor: inputData.timeHozoor.Time,
    dateHozoor: inputData.timeHozoor.Date,
    idPatient: gatheredData.FoundPatient.Id,
  };

  console.log(previousSavedValue.current);
  if (
    Object.values(previousSavedValue.current).length === 0 ||
    JSON.stringify(previousSavedValue.current) !== JSON.stringify(formData)
  ) {
    AddPatientToWorkList(formData);
    previousSavedValue.current = formData;
  } else {
    SwalAlert("warning", "قبلا ثبت شده است", false);
  }
};
