import { env } from 'env';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { Post } from 'Xhr/Xhr';

export const GetInsuranceList = async (
  ApiDataHandler,
  setInsuranceList,
  isPatientList
) => {
  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_GET_INSURANCE_LIST}`,
    true,
    {
      clause: '',
    }
  ).then((response) => {
    if (response.data.resCode === 1)
      isPatientList
        ? setInsuranceList(
            response.data.Data.list.map((elemet) => {
              return {
                ItemValue: elemet.ID,
                ItemContent: ArabicToPersian(elemet.Name),
              };
            })
          )
        : ApiDataHandler(
            'InsuranceList',
            response.data.Data.list.map((elemet) => {
              return {
                ItemValue: elemet.ID,
                ItemContent: ArabicToPersian(elemet.Name),
              };
            })
          );
    else
      SwalAlert('error', response.data.resMessage, false);
  });
};
