import { SearchForPatient } from "../Adapters/SearchForPatient";

export const DetectSearchTypeByTextLength = (
  searchQuery,
  onChange,
  callbackFn
) => {
  // if (typeof searchQuery === Number) {
  //   searchQuery.toString();
  // }
  const EndSearchType =
    searchQuery.length === 11
      ? 4
      : searchQuery.length === 10
      ? 0
      : searchQuery.length <= 7
      ? 1
      : searchQuery.length === 8
      ? 2
      : NaN;

  SearchForPatient(callbackFn, searchQuery, EndSearchType);

  return EndSearchType;
};
