import { Chip, Stack } from "@mui/material";
import ResponsiveChip from "Global/Components/UnEditableComponents/ResponsiveChip/ResponsiveChip";
import React from "react";
import { ArabicToPersian } from "utils/ConvertToPersianTextFromArabic";

const PaitentInformation = ({ patientinfo, eachLangueKey }) => {
  const GeneralInformation = `${ArabicToPersian(
    ArabicToPersian(patientinfo?.FullName)
  )} | ${eachLangueKey[4]} : ${patientinfo?.Id} | ${eachLangueKey[5]} : ${
    patientinfo?.Mobile
  }`;
  const details = `${eachLangueKey[6]} : ${
    ArabicToPersian(patientinfo?.Address) !== ""
      ? ArabicToPersian(patientinfo?.Address)
      : eachLangueKey[7]
  } | ${eachLangueKey[8]} : ${
    patientinfo?.FileNumber ?? `${eachLangueKey[9]}`
  }`;
  return (
    <React.Fragment>
      <Stack width="100%" spacing={1}>
        <ResponsiveChip content={GeneralInformation} />
        <ResponsiveChip content={details} />
      </Stack>
    </React.Fragment>
  );
};

export default PaitentInformation;
