import React from "react";
import { Container, Stack } from "@mui/material";
import { useForm } from "react-hook-form";
import { SearchPatientForReferalDataArray } from "./Variables/SearchPatientForReferalDataArray";
import { SearchPatientForReferalDefaultValues } from "./Variables/SearchPatientForReferalDefaultValues";
import { Search } from "@mui/icons-material";
import { DetectSearchTypeByTextLength } from "./Functions/DetectSearchTypeByTextLength";
import { useMemo } from "react";
import { GetDiagnosisList } from "./Adapters/GetDiagnosisList";
import { GetInsuranceList } from "Global/Adapters/GetInsuranceList";
import MainContainerStyle from "./Styles/Container.Main.Stack.module.scss";
import useGatherDataFromApi from "Global/Hooks/useGatherDataFromApi";
import ReactHookFormGenerator from "Global/ReactHookFormGenerator/ReactHookFormGenerator";
import SubmitButton from "Global/Components/UnEditableComponents/SubmitButton/SubmitButton";
import PersianSupportNumericTextField from "Global/Components/UnEditableComponents/PersianSupportNumericTextField/PersianSupportNumericTextField";
import useGatherDataFromApiOnMount from "Global/Hooks/useGatherDataFromApiOnMount";
import PaitentInformation from "./Components/PaitentInformation/PaitentInformation";
import { AddPatientForNewErjaHandler } from "./Functions/AddPatientForNewErjaHandler";
import { SearchForPatient } from "./Adapters/SearchForPatient";
import { useTranslation } from "react-i18next";
import { useContext } from "react";
import { GlobalContext } from "Global/Context/GlobalContextProvider";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import AccessControl from "Global/security/AccessControl";

const SearchPatientForReferal = () => {
  const getDiagnosisList = useMemo(() => GetDiagnosisList, []);
  const getInsuranceList = useMemo(() => GetInsuranceList, []);
  const { HandleGatherData, gatheredData } = useGatherDataFromApi(
    SearchForPatient,
    false,
    true
  );
  const { apiData } = useGatherDataFromApiOnMount([
    
    getInsuranceList,
  ]);

  const { control, formState, handleSubmit } = useForm({
    defaultValues: SearchPatientForReferalDefaultValues,
  });
  const previousSavedValue = React.useRef({});
  const { t, i18n } = useTranslation();
  const languageArray = useContext(GlobalContext)?.LanguageArray;
  const eachLangueKey =
    [
      // form input labels
      FindLangKey("general-public-label-diagnose", languageArray),
      FindLangKey("general-public-insurance-list", languageArray),
      FindLangKey("general-public-label-diagnose", languageArray),
      FindLangKey("new-referral-input-patient-name-id", languageArray),
      //patient information
      FindLangKey("new-referral-label-id", languageArray),
      FindLangKey("general-public-mobile-number", languageArray),
      FindLangKey("general-public-address", languageArray),
      FindLangKey("new-referral-label-no-address", languageArray),
      FindLangKey("new-referral-label-file-number", languageArray),
      FindLangKey("new-referral-label-no-file-number", languageArray),
    ] || [];

  const btnDisabled = AccessControl("new-referral-btn-add-referral");
  const handleKeyDown = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
    }
  };

  return (
    <React.Fragment>
      <Container>
        <form
          onKeyDown={handleKeyDown}
          onSubmit={handleSubmit((data) => {
            AddPatientForNewErjaHandler(data, gatheredData, previousSavedValue);
          })}
        >
          <Stack
            className={MainContainerStyle.container}
            alignItems="center"
            justifyContent="center"
          >
            <PersianSupportNumericTextField
              label={FindLangKey("new-referral-label-NFI", languageArray)}
              icon={<Search />}
              iconFn={DetectSearchTypeByTextLength}
              callbackFn={HandleGatherData}
              isOctalAllowed={true}
              isEnglish={i18n?.language === "en"}
            />
            {gatheredData instanceof Array === false &&
            gatheredData?.length !== 0 &&
            eachLangueKey.length > 0 &&
            gatheredData ? (
              <React.Fragment>
                <PaitentInformation
                  patientinfo={gatheredData?.FoundPatient}
                  eachLangueKey={eachLangueKey}
                />

                <ReactHookFormGenerator
                  control={control}
                  formState={formState}
                  DataArray={SearchPatientForReferalDataArray(
                    apiData,
                    t,
                    languageArray,
                    eachLangueKey
                  )}
                />

                <SubmitButton label={eachLangueKey[3]} disabled={btnDisabled} />
              </React.Fragment>
            ) : (
              <></>
            )}
          </Stack>
        </form>
      </Container>
    </React.Fragment>
  );
};

export default SearchPatientForReferal;
