import React from "react";
import DateTimePicker from "Global/Components/UnEditableComponents/DateTimePicker/DateTimePicker";
import CustomizableSelect from "Global/Components/CommonComponents/CustomizableSelect/CustomizableSelect";
import PersianSupportTextField from "Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField";

export const SearchPatientForReferalDataArray = (
  parameter,
  t,
  languageArray,
  eachLangueKey
) => {
  const DataArray = [
    {
      element: <DateTimePicker />,
      name: "timeHozoor",
      required: true,
    },

    {
      element: (
        <CustomizableSelect
          SelectableDataArray={parameter?.DiagnosisList}
          label={eachLangueKey[0]}
          isSingle={true}
        />
      ),
      name: "idTashkhis",
      required: true,
    },

    {
      element: (
        <CustomizableSelect
          SelectableDataArray={parameter?.InsuranceList}
          label={eachLangueKey[1]}
          isSingle={true}
        />
      ),
      name: "idInsurance",
      required: true,
    },
    {
      element: <PersianSupportTextField label={eachLangueKey[2]} />,
      name: "description",
    },
    // {
    //   element: <SearchTypePatientList />,
    //   name: "searchType",
    //   required: true,
    // },
  ];
  return DataArray;
};
