import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const SearchForPatient = async (
  callbackFn,
  SearchField,
  typeSearch
) => {
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_FIND_PATIENT}`,
    true,
    {
      SearchField,
      typeSearch,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      callbackFn('FoundPatient', response.data.Data);
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
