import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const GetDiagnosisList = async (ApiDataHandler) => {
  const IdPerson =
    Cookies.get('user') &&
    JSON.parse(Cookies.get('user'))?.IdPerson;
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_DOCTOR_TASHKHIS_LIST}`,
    true,
    {
      idPerson: IdPerson,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      ApiDataHandler(
        'DiagnosisList',
        response?.data?.Data?.list?.map((EeachList) => {
          return {
            ItemValue: EeachList?.Id,
            ItemContent:
              ArabicToPersian(EeachList.Name)?.slice(
                0,
                30
              ) +
              `${EeachList.Name.length > 30 ? '...' : ''}`,
          };
        })
      );
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
